.charts {
  &__row {
    text-align: justify;
    &:after {
      content: '';
      display: inline-block;
      width: 100%;
    }
    >.openVacanc {
      padding-top: 10px;
      color: #0070bb;
      font-size: 15px;
    }
  }
  &__item {
    box-shadow: $shadow;
    display: inline-block;
    height: 205px;
    width: 20.5%;
    vertical-align: top;
    margin-right: .5%;
    &_line {
      width: 100%;
      height: 175px;
    }
  }
  &__tabs {
    width: 37%;
    display: inline-block;
    height: 205px;
    vertical-align: top;
    >.tabsTitles {
      font-size: 14px;
      color: rgb(0, 112, 187);
      margin: -25px 0px 0px 0px;
      >.tabWeek {
        width: 60px;
        display: inline-block;
      }
      >.tabMonth {
        width: 60px;
        display: inline-block;
      }
      >.tabYear {
        width: 60px;
        display: inline-block;
      }
    }
    >.avgSalary {
      word-Break: break-word;
      padding-left: 185px;
      font-size: 14px;
      color: #77c2fd;
      margin-top: -20px;
      position: absolute;
      margin-bottom: -24px;
    }
    >.avgCounter {
      word-Break: break-word;
      padding-left: 185px;
      font-size: 14px;
      color: #818a93;
      margin-top: 5px;
      position: absolute;
      margin-bottom: -24px;
    }
    >div>.historyTitle {
      font-size: 14px;
      margin-left: 170px;
      color: #0070bb;
    }
    >div>.blueHistoryLine {
      background: #0070bb;
      width: 150px;
      height: 4px;
      margin-top: 10px;
      float: left;
    }
    >div>.greeenHistoryLine {
      background: #39b549;
      width: 150px;
      height: 4px;
      margin-top: 10px;
      float: left;
    }
  }
  &__tabs-group {
    font-size: 0;
  }
  &__tabs-tab {
    display: inline-block;
    cursor: pointer;
    height: 30px;
    width: 30px;
    text-align: center;
    padding: 5px 5px;
    font-size: 14px;
    vertical-align: top;
    border-radius: 3px 3px 0 0;
    background: lighten($c-back, 4);
    border-right: 1px solid $c-back;
    &:not(._tab-active) {
      &:hover {
        background: lighten($c-back, 2);
      }
    }
    &._tab-active {
      background: #ffffff;
    }
  }
  &__tabs-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: #fff;
    border: 1px solid #77c2fd;
    overflow: hidden;
    position: relative;
    &.grey-icon {
      display: inline-block;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background: #fff;
      border: 1px solid #818a93;
      overflow: hidden;
      position: relative;
    }
    &_quarter {
      &:before {
        content: '';
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        background: #77c2fd;
        top: -10px;
        left: -10px;
      }
      &.grey-icon {
        &:before {
          content: '';
          display: block;
          position: absolute;
          width: 20px;
          height: 20px;
          background: #818a93;
          top: -10px;
          left: -10px;
        }
      }
    }
    &_halve {
      &:before {
        content: '';
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        background: #77c2fd;
        top: -10px;
        left: 0;
      }
      &.grey-icon {
        &:before {
          content: '';
          display: block;
          position: absolute;
          width: 20px;
          height: 20px;
          background: #818a93;
          top: -10px;
          left: 0;
        }
      }
    }
    &_full {
      background: #77c2fd;
      &.grey-icon {
        background: #818a93;
      }
    }
  }
  &__tab-content {
    display: none;
    &._visible {
      display: block;
      animation: fadeIn 400ms;
    }
  }
}